import VueAxios from 'vue-axios';
import axios from 'axios';
Vue.use(VueAxios, axios);
import { githubId } from '@/api/user';
import Vue from 'vue';
import VueAuthenticate from 'vue-authenticate';
export default {
  name: 'SocialSignin',
  data: function data() {
    return {};
  },
  created: function created() {
    this.loadAuthInfo();
  },
  methods: {
    loadAuthInfo: function loadAuthInfo() {
      githubId().then(function (_ref) {
        var data = _ref.data;
        var authOption = {
          baseUrl: window.location.origin,
          // Your API domain
          providers: {
            github: {
              clientId: data.clientID,
              scope: ['user:email', 'read:user', 'read:org'],
              redirectUri: "".concat(window.location.origin, "/auth/github") // Your client app URL

            }
          }
        };
        Vue.use(VueAuthenticate, authOption);
      }).catch(function (error) {
        console.error(error);
      });
    },
    authenticate: function authenticate(provider) {
      this.$auth.authenticate(provider).then(function (data) {
        // Execute application logic after successful social authentication
        console.log('login success', data);
      }).catch(function (error) {
        console.error("login failed ".concat(error.message));
        console.error(error);
      });
